body {
  margin: 0;
  font-family: "Orbitron", sans-serif;
  background: radial-gradient(circle at top, #0b0c10, #1f2833);
  color: #c5c6c7;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.calculator-container {
  background: rgba(31, 40, 51, 0.9);
  padding: 2rem;
  border-radius: 20px;
  box-shadow: 0 0 30px rgba(0, 200, 255, 0.6);
  text-align: center;
  max-width: 400px;
  width: 90%;
  animation: float 6s ease-in-out infinite;
}

h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #66fcf1;
  text-shadow: 0 0 15px #45a29e;
}

input {
  width: 80%;
  padding: 0.6rem;
  margin: 0.5rem 0;
  border: none;
  border-radius: 10px;
  text-align: center;
  font-size: 1rem;
}

button {
  margin-top: 1rem;
  padding: 0.7rem 1.5rem;
  border: none;
  border-radius: 50px;
  background: linear-gradient(90deg, #45a29e, #66fcf1);
  color: #0b0c10;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
}

button:hover {
  transform: scale(1.1);
  box-shadow: 0 0 20px #66fcf1;
}

.result {
  margin-top: 1rem;
  font-size: 1.2rem;
  color: #ffffff;
}

.rocket {
  font-size: 3rem;
  margin-top: 1rem;
  animation: launch 2s ease-in-out forwards;
}

/* Floating container */
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

/* Rocket launch animation */
@keyframes launch {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(-300px);
    opacity: 0;
  }
}
